* {
  margin: 0;
  padding: 0;
}

.navbar img,
.menuContainer img {
  width: 25px;
  height: 25px;
}

a {
  display: flex;
  justify-content: space-between;
  text-decoration: none;
}

.wrapper {
  display: block;
  position: fixed; /*sticky OR fixed*/
  /* overflow: hidden; */
  height: 16%;
  width: 100%;
  top: 0;
  z-index: 1;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #212121;
  margin: 0 auto;
  height: 70px;
  width: 100%;
  padding: 0 10px 0 0;
}

.title {
  color: white;
  transition: 0.3;
}

.title:hover{
  color: lightgrey;
}

.titleContainer {
  height: 30px;
  width: 150px;
  display: flex;
  align-content: center;
  padding: 0 10px;
}

.navbar {
  display: flex;
  background-color: #212121;
  border: 2px solid white;
  border-radius: 18px;
  overflow: hidden;
  padding: 5px 5px;
}

.navbar a {
  float: left;
  text-align: center;
  text-decoration: none;
  padding: 10px 16px;
  font-size: 17px;
  width: 98px;
  border-radius: 12px;
  color: #f2f2f2;
  transition: 0.3s;
}

.navbar a:hover {
  background-color: #474747;
}

.navbar a.active {
  background-color: #474747;
  color: white;
}

.menuContainer {
  width: 40px;
}

.menu {
  float: right;
  padding: 10px 10px;
  margin: 0 10px;
  width: 25px;
  background-color: transparent;
  border: transparent;
  border-radius: 12px;
  transition: 0.3s;
}

.menu:hover {
  background-color: #474747;
}

@media screen and (max-width: 768px) {
  .navbar p {
    display: none;
    padding: 0px 0px;
    width: 0px;
    height: 0px;
  }

  .navbar a {
    padding: 4px 8px;
    width: 40px;
    justify-content: center;
    /*navbar{padding 0 0}*/
  }

  .navbar {
    padding: 1px 1px;
  }

  .menuContainer {
    width: 40px;
  }

  .menu {
    padding: 6px 6px;
  }

  .title {
    font-size: 20px;
  }

  .titleContainer {
    height: 50px;
    width: 60px;
  }
}
