.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.exercisesContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}

@media screen and (max-width: 768px){
  .exercisesContainer {
    flex-direction: column;
    align-items: center;
  }
}