.container {
  display: flex;
  flex-direction: column;
  flex: 1, 1, 333.3px;
  width: 28%;
  height: 24rem;
  background-color: #f0f0f0;
  border-radius: 25px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  margin: 15px 10px;
  box-sizing: border-box;
}

.imageRecipe {
  width: 100%;
  height: auto;
  max-height: 55%;
  border-radius: 25px 25px 0 0;
  background-size: cover;
}

.titleContainer {
  width: 100%;
  text-align: left;
  padding: 5px 0;
}

.titleContainer h3{
  padding: 0 5px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.descriptionContainer{
  padding: 5px 5px 0 5px;
}

.descriptionContainer p{
  overflow: hidden; /*NOTE: margins, adaptive, button, <p> text overflow*/
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4; /* number of lines to show */
  -webkit-box-orient: vertical;
}

.buttonContainer {
  position: relative;
  text-align: center;
  top: 1rem;
}

.buttonRecipe {
  width: 50%;
  height: 2.5rem;
  background-color: inherit;
  color: black;
  border-radius: 20px;
  border: 2px solid black;
  font-size: 16px;
  font-weight: bold;
  transition: 0.3s;
}

.buttonRecipe:hover {
  background-color: rgb(88, 88, 88);
}

@media screen and (max-width: 768px){
  .container{
    width: 80%;
  }
  .buttonRecipe{
    width: 90%;
  }
}
