.pagesContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  background-image:linear-gradient(rgba(0, 0, 0, 0.327), rgba(0, 0, 0, 0.4)), url("../images/wallpaper.jpg");
  background-position: 80% 35%;
  height: 400px;
  width: 1200px;
  box-shadow: -4px 4px 4px rgba(0, 0, 0, 0.4);
  margin: 0 0 40px 0;
}

.pageItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 245px;
  height: 300px; /*320*/
  border: solid white;
  border-radius: 30px;
  background-color: rgba(255, 255, 255, 0.8);
  color: #474747;
  margin: 15px;
  overflow: hidden;
  transition: 0.1s;
}

.pageItem:hover{
  box-shadow: 1px 1px 1px 1px rgba(255, 255, 255, 0.6);
}

.pageItem h2 {
  padding: 4px;
}

.pageItem p {
  height: 120px;
  max-height: 120px; /* max-height: 124px; */
  max-width: 240px;
  font-size: 15px;
  font-weight: 600;
  padding: 5px 5px;

  /* overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; NOTE: Delete this shit, but replace some code */
}

.pageItem img {
  width: 245px;
  height: 150px;
  margin-top: auto;
  /* flex-wrap: wrap; */
}
