.container{
    width: 1200px;
    height: 300px;
    box-shadow: -4px 4px 4px rgba(0, 0, 0, 0.4);
    background-color: rgb(74, 80, 85);
    color: white;
}

.container h2{
    margin: 0 0 15px 0;
    padding: 10px 0;
    text-align: center;
    text-decoration: 1px underline;
}

.container p {
    padding: 20px 20px;
    font-size: 18px;
}