.contentWrapper{
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  width: 90%;
  margin: 15px 0;
  /*Cross-browser border solution */
  /* filter: dropshadow(0px 0px 0px 2.5px black); 
  box-shadow: 0px 0px 0px 2.5px black;    */
  /* border: 3px solid black; */
  border-radius: 25px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
}

.titleContainer {
  width: 100%;
  background-color: #212121;
  color: white;
  text-align: center;
  padding: 5px 0px;
  border-radius: 20px 20px 0 0;
}

.titleContainer h3{
  padding: 0 10px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.contentContainer {
  float: left;
  padding: 0.5rem;
  background-color: #f0f0f0;
  border-radius: 0 0 20px 20px;
}

.contentContainer p{
  display: block;
  overflow: hidden;
  max-height: 100px;
  line-height: 20px;;
}

.buttonNews {
  width: 6rem;
  height: 2.5rem;
  background-color: inherit;
  color: black;
  font-size: 16px;
  font-weight: bold;
  float: right;
  transition: 0.3s;
  border: 2px solid black;
  border-radius: 20px;
}

.buttonNews:hover {
  background-color: rgb(88, 88, 88);
}

@media screen and (max-width: 768px){
  .buttonNews {
    width: 100%;
  }
}
