.container {
  display: flex;
  flex-direction: column;
  width: 22rem;
  max-width: 22rem;
  height: 20rem;
  border: 3px solid black;
  margin: 20px 20px;
  flex-grow: 1;
  flex-shrink: 1;
}

.itemPhotoContainer {
  flex: 0 1 50%;
  max-width: 22rem;
  width: 22rem;
  border-bottom: 2px solid black;
  /* padding: 5px; */
}
.itemPhoto {
  max-height: 14rem;
  max-width: 22rem;
  width: 22rem;
  /* margin: 5px; */
}
.description {
  padding: 0.25rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.title {
  text-transform: uppercase;
}

.itemButton {
  margin: 0rem 0.25rem 0 0.25rem;
  float: right;
  width: 25%;
  height: 5rem;
  background-color: black;
  color: white;
  border-radius: 20px;
  font-size: 16px;
  font-weight: bold;
}
.itemButton:hover {
  background-color: rgb(77, 77, 77);
}
