.container {
  display: flex;
  flex-direction: column;
  flex: 1, 1, 333.3px;
  width: 28%;
  height: 24rem;
  background-color: #f0f0f0;
  border-radius: 25px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  margin: 20px 20px;
}

.imageExercise {
  width: 100%;
  max-height: 50%;/*solve the problem with img size*/
  background-size: cover;
}

.titleContainer {
  padding: 0.25rem;
  background-color: #212121;
  color: white;
  border-radius: 25px 25px 0 0;
  text-align: center;
}

/* .content {
  display: flex;
  flex-direction: column;
  align-items: center; DELETE THIS, copy structure from recipes and fix a button
} */
.descriptionContainer {
  padding: 5px 5px 0 5px;
}

.descriptionContainer p{
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4; /* number of lines to show */
  -webkit-box-orient: vertical;
}

/* .description { DELETE THIS
  padding: 0.18rem;
  max-height: 5rem;
  flex: 1 0 7rem;
  overflow: hidden;
} */

.buttonContainer{
  position: relative;
  text-align: center;
  top: 1rem;
  /* width: 60%; */
}

.buttonExercise {
  width: 50%;
  height: 2.5rem;
  background-color: inherit;
  color: black;
  border-radius: 20px;
  border: 2px solid black;
  font-size: 16px;
  font-weight: bold;
  transition: 0.3s;
}

.buttonExercise:hover {
  background-color: rgb(77, 77, 77);
}

@media screen and (max-width: 768px){
  .container {
    width: 80%;
  }
  .buttonExercise{
    width: 90%;
  }
}